.extra-curricular-page {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(8, 1fr);
  height: 120vh;
}
.extra-curricular-image {
  grid-column: 1/4;
  grid-row: 1/2;
  padding: 5%;
}
.extra-curricular-title-container {
  grid-column: 4/9;
  text-align: center;
  align-content: center;
  font-size: 115%;
  font-weight: 800;
  padding-right: 5%;
}

.extra-curricular-back-next-btn {
  grid-row: 8/9;
  grid-column: 2/8;
  display: flex;
  flex-direction: row;
}
.extra-curricular-back-next-btn button {
  height: 50%;
  width: 40%;
}

@media (min-width: 1400px) and (max-width: 1600px) {
  .extra-curricular-page {
    display: grid;
    grid-template-columns: repeat(16, 1fr);
    grid-template-rows: repeat(16, 1fr);
  }
  .extra-curricular-title-container {
    grid-column: 1/12;
    grid-row: 1/3;
    font-size: 190%;
    padding-bottom: 9%;
  }
  .extra-curricular-image {
    grid-column: 12/16;
    grid-row: 3/13;
    padding: 0%;
    transform: scaleX(-1);
  }
  .extra-curricular-back-next-btn {
    grid-row: 13/16;
    grid-column: 3/10;
    padding-bottom: 5%;
  }

  .extra-curricular-back-next-btn button {
    width: 30%;
    height: 70%;
  }
  .extra-curricular-page {
    height: 90vh;
  }
}
