.O-Level-result-container {
  border: 3px solid black;
  border-radius: 10px;
}

.O-Level-result-btn-container {
  overflow-y: scroll;
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  gap: 10px;
  flex-wrap: wrap;
  height: 85%;
  width: 95%;
}

.O-Level-result-NextAndBackbtn-container {
  display: flex;
  flex-direction: row;
  gap: 20%;
  margin-top: 5%;
  margin-left: 18%;
  width: 60%;
}

.O-Level-result-NextAndBackbtn-container button {
  width: 100%;
  height: 100%;
}

.O-Level-result-container h2 {
  text-align: center;
  margin-top: 1.5%;
}

.O-level-result-btn {
  height: 100px;
  width: 300px;
  border: 1px solid black;
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  gap: 5%;
}

.O-level-result-btn h4 {
  padding-left: 5%;
  padding-top: 2%;
  font-size: 130%;
  width: 40%;
  margin-top: auto;
  margin-bottom: auto;
}

.dropbtn {
  background-color: #ffffff;
  color: rgb(0, 0, 0);
  padding: 10px;
  font-size: 16px;
  border: none;
  cursor: pointer;
  border: 1px solid black;
}

.dropdown {
  margin-top: auto;
  margin-bottom: auto;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  cursor: pointer;
}

.dropdown-content a:hover {
  background-color: #f1f1f1;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .dropbtn {
  background-color: #969393;
}
